* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font: 18px/1.5 -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0 0 .5em;
}

p {
  margin: 0 0 1em;
}

header {
  background: #eee;
}

nav {
  padding: 0 40px;
  width: 1200px;
  max-width: 100%;
  margin: 0 auto;
}

nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

nav ul li {
  display: inline-block;
  padding: 10px 10px 10px 0;
}

nav ul li a {
  color: #36e;
}

main {
  padding: 40px;
  width: 1200px;
  max-width: 100%;
  margin: 0 auto;
}

.article-list--item {
  text-decoration: none;
  color: black;
  margin: 0 0 20px;
  display: block;
  border-bottom: 1px solid black;
}

.article-list--item:hover {
  text-decoration: underline;
}

.article-list--item h2 {
  font-size: 22px;
  margin: 0;
}

.upvotes button { background: #eee; color: black; border: 1px solid #ccc; font: inherit; line-height: 1.15; padding: 4px 10px; margin: 0 15px 15px 0; cursor: pointer; }

.comment-form { padding: 0 0 20px; }
.comment-form label { display: block; width: 100%; padding: 5px 0; }
.comment-form label input, .comment-form label textarea { display: block; font: inherit; line-height: 1.2; padding: 5px 10px; }
.comment-form button { background: #eee; border: 1px solid #ccc; color: black; font: inherit; line-height: 1.2; padding: 5px 10px; margin: 10px 0; }